.socials {
  font-family: $poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
}

.socials__list {
  list-style: none;
  margin: 0;
  padding: 0;

  @media (max-width: $s-end) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

}

.socials__item {
  margin-bottom: 22px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (max-width: $s-end) {
    margin-right: 15px;
    margin-bottom: 7px;

    &:last-child{
      margin-right: 0;
      margin-bottom: 7px;
    }
  }
}


.socials__link {
  text-decoration: none;

  @media (max-width: $s-end) {
    span {
      display: none;
    }
  }
}

.socials__svg {
  margin-right: 15px;

  @media (max-width: $m-end) {
    margin-right: 12px;
  }

  @media (max-width: $s-end) {
    width: 20px;
    height: 21px;
  }
}
