.subs-form {
  position: relative;

  input {
    width: 100%;
    height: 40px;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $white;
    color: $white;
    font-family: $poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.3;
    padding-left: 5px;
    padding-right: 125px;

    &::placeholder {
      font-family: $poppins;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 21px;
      color: $light-gary;
    }
  }

  @media (max-width: $m-end) {
    max-width: 80%;
  }

  @media (max-width: $s-end) {
    max-width: unset;
  }
}

.subs-form__btn {
  background-color: transparent;
  border: none;
  position: absolute;
  right: 0;
  top: 0;
  padding: 10px 0 10px 5px;


  span {
    font-family: $poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.3;
    color: $white;

  }

  svg {
    margin-left: 7px;
  }
}
